@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;0,600;0,700;0,800;0,900;1,400;1,800;1,900&display=swap');
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
:root{
    --primaryColor : hsl(199,100%,33%); /*hsl(hue, saturation, lightness)*/
    --secondaryColor : hsl(187,85%,43%);
    --background :  linear-gradient(to right, #09203f 0%, #537895 100%);
    --whiteColor : hsl(0,0%,100%);
    --blackColor : hsl(201,33%,16%);
    --textColor : hsl(240,4%,36%);
    --whiteColorDeam : hsl(0,0%,93%);
    --greyText : rgb(190,190,190);
    --inputColor : rgb(239,239,239);
    --bodyColor : rgb(240,246,246);
    --cardBG : rgb(225,225,235);
}
a{
    text-decoration: none;
}
li{
    list-style-type: none;
}
.section{
    padding: 4rem 0 2rem;
}
.container{
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}
.icon{
    font-size: 2rem;
    cursor: pointer;
}
.flex{
    display: flex;
    align-items: center;
}
.grid{
    display: grid;
    align-items: center;
}
.btn{
    padding: 0.6rem 1.5rem ;
    background: var(--background);
    border-radius: 3rem;
    border: none;
    outline: none;
    cursor: pointer;
}
.btn a{
    color: var(--whiteColor);
    font-weight: 500;
}
.btn:hover {
    background-color: var(--secondaryColor);
}
img,video{
    width: 100%;
    height: auto;
}
input{
    font-size: 100%;

}
body{
    background-color: var(--bodyColor);
}