:root{
    --primaryColor : hsl(199,100%,33%); /*hsl(hue, saturation, lightness)*/
    --secondaryColor : hsl(187,85%,43%);
    --background :  linear-gradient(to right, #09203f 0%, #537895 100%);
    --whiteColor : hsl(0,0%,100%);
    --blackColor : hsl(201,33%,16%);
    --textColor : hsl(240,4%,36%);
    --whiteColorDeam : hsl(0,0%,93%);
    --greyText : rgb(190,190,190);
    --inputColor : rgb(239,239,239);
    --bodyColor : rgb(240,246,246);
    --cardBG : rgb(225,225,235);
}
.header{
    position:fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--whiteColorDeam);
    width: 100%;
    padding: 1rem;
    z-index: 1000;
    box-shadow: 0 2px 4px rgba(2, 15, 29, 0.42);

    .logo{
        color: var(--blackColor);
        font-weight: 600;
        cursor: pointer;
        align-items: center;
        justify-content: center;

        .icon{
            font-size: 25px;
            color: var(--primaryColor);
            transform: translateY(5px);
        }
    }
    @media screen and (max-width : 768px) {
        .navBar{
            position: absolute;
            background: var(--whiteColorDeam);
            height: max-content;
            width: 80%;
            border-radius: 1rem;
            top: 500rem;
            left: 50%;
            padding:1rem;
            transform: translate(-50%);
            z-index: 2000;
            box-shadow: 0 2px 4px rgba(2,15,29,0.904);
            transition: 1s ease-in-out;
            .navLists{
                flex-direction: column;
                justify-content: center;
                width: 100%;
                align-items: center;
                margin: auto;
                padding: 0.5rem 0;

                .navItem{
                    padding: 0.5rem 0;
                    color: var(--textColor);

                    .navLink{
                        color: var(--textColor);
                        font-size: 0.9rem;
                        font-weight: 600;
                        &:hover{
                            color: var(--primaryColor)
                        }
                    }
                
                } 
                .btn{
                    margin-top: 1rem;
                    a{  
                        font-weight: 600;
                        color: var(--whiteColor);
                    }
                }   
            }
            .closeNavBar{
                position: absolute;
                top: 1rem;
                right: 1.5rem;
                color: var(--primaryColor);
                &:hover{
                    color: var(--secondaryColor);
                }
            }
        }
        .activeNavbar{
            top: 7rem;
        }
    }

    .toggleNavbar{
        .icon{
            font-size: 25px;
            color: var(--primaryColor);
        
        &:hover.icon{
            color: var(--secondaryColor);
        }
        }
    }
    
}